.content-grid-section {
  padding: 56px 0 0 0;
  background: var(--surface-alt, #F8F9FA);
}

.content-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 32px 24px;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 24px;
}

.app-card {
  background: var(--surface, #fff);
  border-radius: var(--radius-card, 16px);
  box-shadow: var(--elevation-1, 0 1px 2px rgba(0,0,0,0.08));
  transition: box-shadow 0.2s, transform 0.2s;
  display: flex;
  flex-direction: column;
  outline: none;
}
.app-card:focus,
.app-card:hover {
  box-shadow: var(--elevation-3, 0 6px 12px rgba(0,0,0,0.12));
  transform: scale(1.01);
}
.app-card-image-wrapper {
  width: 100%;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 16px 16px 0 0;
  background: #e0e3e7;
  display: flex;
  align-items: center;
  justify-content: center;
}
.app-card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 16px 16px 0 0;
  background: #e0e3e7;
}
.app-card-content {
  padding: 16px 16px 20px 16px;
  display: flex;
  flex-direction: column;
  flex: 1;
}
.app-card-title {
  font-family: 'Google Sans', 'Roboto', Arial, sans-serif;
  font-size: 1.25rem;
  font-weight: 500;
  color: var(--text-primary, #202124);
  margin: 0 0 4px 0;
}
.app-card-meta {
  display: flex;
  align-items: center;
  font-size: 0.95rem;
  color: var(--text-secondary, #5F6368);
  margin-bottom: 8px;
  gap: 8px;
}
.app-card-developer {
  font-size: 0.95rem;
  color: var(--text-secondary, #5F6368);
}
.app-card-rating {
  display: flex;
  align-items: center;
  gap: 2px;
  font-size: 1rem;
  color: #FBBC05;
  margin-left: 8px;
}
.app-card-rating .material-symbols-rounded {
  font-size: 1.1em;
  vertical-align: middle;
  color: #FBBC05;
  margin-right: 2px;
}
.app-card-reviews {
  color: var(--text-secondary, #5F6368);
  font-size: 0.95em;
  margin-left: 2px;
}
.app-card-tags {
  margin-bottom: 10px;
}
.app-card-tag {
  display: inline-block;
  background: var(--color-accent-blue, #4285F4);
  color: #fff;
  font-size: 0.8rem;
  border-radius: 8px;
  padding: 2px 10px;
  margin-right: 6px;
  margin-bottom: 2px;
}
.app-card-install-btn {
  margin-top: auto;
  background: var(--color-primary, #34A853);
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 8px 0;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s;
  box-shadow: 0 1px 2px rgba(52,168,83,0.08);
}
.app-card-install-btn:hover,
.app-card-install-btn:focus {
  background: #24913c;
}
@media (max-width: 600px) {
  .content-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px 12px;
    padding: 0 8px;
  }
  .content-grid-section {
    padding: 32px 0 0 0;
  }
}
@media (min-width: 601px) and (max-width: 1024px) {
  .content-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 24px 16px;
    padding: 0 16px;
  }
}
